import React from "react";
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
const axios = require('axios');


const styles = theme => ({
    main: {
        width: 'auto',
        display: 'block', // Fix IE 11 issue.
        marginLeft: theme.spacing.unit * 3,
        marginRight: theme.spacing.unit * 3,
        [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
            width: 800,
            marginLeft: 'auto',
            marginRight: 'auto',
        },
    },
    results: {
        width: 'auto',
        display: 'block', // Fix IE 11 issue.
        marginLeft: theme.spacing.unit * 3,
        marginRight: theme.spacing.unit * 3,
        marginTop: theme.spacing.unit * 3,
        marginBottom: theme.spacing.unit * 3,
       
    },
    paper: {
        marginTop: theme.spacing.unit * 4,
        marginBottom: theme.spacing.unit * 8,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: `${theme.spacing(5)}px ${theme.spacing(5)}px ${theme.spacing.unit * 3}px`,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing.unit,
    },
    submit: {
        marginTop: theme.spacing.unit * 3,
    },
    table: {
        minWidth: 650,
      },
})


class HomePage extends React.Component {
    constructor(props) {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.state = {
            isButtonPressed: false,
            visited : null,
            logos : null,
            people : null,
            images : null,
            url: null,
           
        }
    }
    handleSubmit= async  (e) => {
        e.preventDefault();
        this.setState({isButtonPressed:true, visited: null, logos: null, people: null, images: null});
        const url = e.target.elements.url.value.trim();
        console.log(url);
        let serverRequest = `http://localhost:8080/main?url=${url}&depth=2`;
        let result = await axios.post(serverRequest).catch(error => console.log(error))
        // this.setState({res:result})
        console.log(result.data.split(" "));
        let visited = result.data.split(" ")[0];
        let people = result.data.split(" ")[1];
        let logos = result.data.split(" ")[2];
        let img = result.data.split(" ")[3];
        visited = visited.substring(visited.indexOf('['), visited.indexOf(']') + 1);
        logos = logos.substring(logos.indexOf('['), logos.indexOf(']') + 1)
        people = people.substring(people.indexOf('['), people.indexOf(']') + 1);
        img  = img.substring(img.indexOf('['), img.indexOf(']') + 1);
        visited = JSON.parse(visited)
        logos = JSON.parse(logos);
        people = JSON.parse(people);
        img =  JSON.parse(img);
        console.log("visited : ", visited.length)
        console.log("logos : ", logos.length)
        console.log("people : ", people.length)
        console.log("img : ", img.length)
        // console.log(letters.substring(letters.indexOf('['), letters.indexOf(']') + 1))
        this.setState({isButtonPressed:false, visited: visited, logos: logos, people: people, images: img, url: url});
        console.log(this.state.images.length)
    }

    render() {
        const { classes } = this.props;
     
      
        return (
            <div>
                <div className={classes.main}>
                    <CssBaseline />
                    <Paper className={classes.paper} elevation={12}>
                    <form className={classes.form} onSubmit={this.handleSubmit}>
                            <Typography color='primary' component="h1" variant="h5">
                                Welcome
                            </Typography>
                            
                            <Typography >
                                This WebPage Performs a web crawl on the url String provided by the user, Mutiple subpages are crawled to find more Images, subpages are limited to 100 considering time constrains, Multi threading is implemented to crawl multiple subpages at once.
                            </Typography>
                            <Typography >
                                NOTE: Crawling may take a while, depending upon the number of links(sublinks) and images as Face Recognization algorithm is implemented
                            </Typography>
                            <TextField
                                label="Url"
                                className={classes.textField}
                                id="url"
                                placeholder="Enter the url here"
                                type="url"
                                inputProps={{ minLength: 10 }}
                                margin="normal"
                                variant="outlined"
                                required={true}
                                fullWidth
                            />
                            
                            <Button
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                disabled={this.state.isButtonPressed}
                                className={classes.submit}
                            >
                                {!this.state.isButtonPressed ? "Crawl url" : "Processing Request..."}
                            </Button>
                        </form>
                    </Paper>
                </div>
                {this.state.images != null  ?
                    <div className={classes.main}>
                    <Typography color='primary' component="h1" variant="h5">
                            Statistics
                    </Typography>
                    <CssBaseline />
                    <TableContainer component={Paper}>
                    <Table className={classes.table} aria-label="simple table">
                      <TableHead>
                        <TableRow>
                          <TableCell>URl provided</TableCell>
                          <TableCell align="right">Total links Visited</TableCell>
                          <TableCell align="right">Total Logos found</TableCell>
                          <TableCell align="right">Images that Contain People</TableCell>
                          <TableCell align="right">Total Images</TableCell>
                        </TableRow>
                      </TableHead>
                      <TableBody>
                          <TableRow>
                            <TableCell component="th" scope="row">
                              {this.state.url}
                            </TableCell>
                            <TableCell align="right">{this.state.visited.length}</TableCell>
                            <TableCell align="right">{this.state.logos.length}</TableCell>
                            <TableCell align="right">{this.state.people.length}</TableCell>
                            <TableCell align="right">{this.state.images.length}</TableCell>
                          </TableRow>
                      </TableBody>
                    </Table>
                  </TableContainer>
                  </div>
                    :
                    <div ></div>
                }
               
                {(this.state.logos != null && this.state.logos.length != 0)  ?
                    <div className={classes.results}>
                         <Typography color='primary' component="h1" variant="h5">
                         Total Logo( s) :{this.state.logos.length} 
                        </Typography>
                        {/* <div className="px-5 py-2 h6 text-light">{`Total Logo( s) : ${this.state.logos.length}`}</div> */}
                        <div className="imageList p-5">
                            {this.state.logos.map((imageUrl) => <img height="100" src={imageUrl} onerror="this.style.display='none'"/>)}
                        </div>
                    </div>
                    :
                    <div className="text-danger mx-2 px-5 py-2"></div>
                }
                {(this.state.people != null && this.state.people.length != 0)  ?
                    <div className={classes.results}>
                        <Typography  component="h1">
                        Total Images containing People :{this.state.logos.people} 
                        </Typography>
                        <div className="imageList p-5">
                            {this.state.people.map((imageUrl) => <img height="100" src={imageUrl} onerror="this.style.display='none'"/>)}
                        </div>
                    </div>
                    :
                    <div className="text-danger mx-2 px-5 py-2"></div>
                }
                {(this.state.images != null && this.state.images.length != 0)  ?
                    <div className={classes.results}>
                        <Typography  component="h1">
                        Total Images :{this.state.images.length} 
                        </Typography>
                        <div className="imageList p-5">
                            {this.state.images.map((imageUrl) => <img height="100" src={imageUrl} onerror="this.style.display='none'"/>)}
                        </div>
                    </div>
                    :
                    <div className="text-danger mx-2 px-5 py-2"></div>
                }
               
               {(this.state.visited != null && this.state.visited.length != 0)  ?
                    <div className={classes.results}>
                        <Typography  component="h1">
                        Total Links Visited :{this.state.visited.length} 
                        </Typography>
                        <div className="imageList p-5">
                            {this.state.visited.map((link) =><div> {link}</div>)}
                        </div>
                    </div>
                    :
                    <div className="text-danger mx-2 px-5 py-2"></div>
                }
              
                
            </div>
        )
    }
}


export default withStyles(styles)(HomePage);